import React from 'react';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import { Link } from 'react-router';

const FormMovie = () => {
  console.log('sadffds');
  // onSignIn = (e) => {
  //   const { boundSignIn } = this.props;
  //   boundSignIn({
  //     email: this.emailField.input.value,
  //     password: this.passwordField.input.value
  //   });
  // };

  return (
    <div className="flex-container form-container">
      <div className="flex-container movie-form">
        <TextField floatingLabelText="Name" fullWidth ref={(name) => { this.name = name; }}/>
        <TextField
          floatingLabelText="OriginalName"
          fullWidth
          ref={(originalName) => { this.originalName = originalName; }}
        />
        <TextField floatingLabelText="Img" fullWidth ref={(img) => { this.img = img; }}/>
        <TextField floatingLabelText="Country" fullWidth ref={(contry) => { this.contry = contry; }}/>
        <TextField
          floatingLabelText="Release Date"
          fullWidth ref={(releaseDate) => { this.releaseDate = releaseDate; }}
        />
        <TextField floatingLabelText="During" fullWidth ref={(during) => { this.during = during; }}/>

        <Link to="/signup" style={{ width: 'inherit' }}>
          <RaisedButton label="New" fullWidth className="form-btn"/>
        </Link>

        <Link to="/signup" style={{ width: 'inherit' }}>
          <RaisedButton label="Back" fullWidth className="form-btn"/>
        </Link>
      </div>
    </div>
  );
};

export default FormMovie;



